body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  color: rgba(0, 0, 0, 0.9); }

body {
  min-height: 100vh;
  background-color: whitesmoke; }

* {
  box-sizing: border-box;
  -webkit-box-sizing: border-box; }

a {
  color: rgba(0, 0, 0, 0.9);
  text-decoration: none; }

#root {
  overflow: hidden; }

.header {
  height: 40px;
  display: block; }

.placeholder {
  width: 100%;
  display: block;
  background: #f2f2f2;
  padding-bottom: 56.5%; }

.sources {
  position: fixed;
  z-index: 9;
  top: 0;
  left: 0;
  width: 270px;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
  background: rgba(0, 0, 0, 0.06);
  text-align: left;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s; }

@media (min-width: 767px) {
  .sources {
    display: block !important; } }

.sources-list {
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
  position: absolute;
  top: 95px;
  width: 100%;
  padding: 20px; }

.sources-list--left {
  left: 0; }

.sources-list--right {
  left: 100%; }

.item {
  float: left;
  width: 100%; }

.item--simple {
  text-transform: Capitalize; }

.item label {
  padding: 10px 20px 10px 25px;
  float: left;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 2px;
  border: 1px solid transparent;
  text-align: left;
  position: relative;
  -webkit-transition: 0.15s;
  -o-transition: 0.15s;
  transition: 0.15s; }

.item input[type="checkbox"] {
  visibility: hidden;
  height: 0;
  width: 0;
  margin: 0;
  padding: 0; }

.item.item--simple label:before {
  content: "";
  position: absolute;
  width: 18px;
  height: 2px;
  background: red;
  left: 0;
  top: 18px;
  opacity: 0; }

.item input[type="checkbox"]:checked + label {
  background: white;
  border-radius: 5px;
  border: 1px solid #d8dbdc;
  padding-left: 36px;
  -webkit-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.1);
          box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.1); }

.item.item--simple input[type="checkbox"]:checked + label:before {
  opacity: 1;
  -webkit-transform: translate3d(10px, 0, 0);
          transform: translate3d(10px, 0, 0); }

.item--plusminus label:before,
.item--plusminus label:after {
  content: "";
  height: 1px;
  width: 20px;
  position: absolute;
  top: 20px;
  left: calc(100% - 35px);
  -webkit-transform: translate3d(0, 0, 0) translate(-50%, -50%);
          transform: translate3d(0, 0, 0) translate(-50%, -50%);
  background: #000; }

.item--plusminus label:after {
  content: "";
  -webkit-transform: rotate(90deg) translate(-50%, -50%);
      -ms-transform: rotate(90deg) translate(-50%, -50%);
          transform: rotate(90deg) translate(-50%, -50%);
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
  -webkit-transform-origin: 0 0;
      -ms-transform-origin: 0 0;
          transform-origin: 0 0; }

.item--plusminus input[type=checkbox]:checked + label:after {
  background: #e1e3e3;
  -webkit-transform: rotate(0) translate(-50%, -50%);
      -ms-transform: rotate(0) translate(-50%, -50%);
          transform: rotate(0) translate(-50%, -50%); }

.trigger {
  position: fixed;
  z-index: 10;
  left: 10px;
  top: 10px;
  width: 40px;
  height: 40px;
  background: white;
  -webkit-appearance: none;
  border-radius: 0px;
  -webkit-box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.17);
  box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.17); }

.trigger:after,
.trigger:before {
  content: "";
  display: block;
  width: 24px;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  left: 8px;
  top: 16px;
  -webkit-transition-duration: 0.15s;
       -o-transition-duration: 0.15s;
          transition-duration: 0.15s; }

.trigger:after {
  top: 22px; }

@media (min-width: 767px) {
  .trigger {
    display: none; } }

.trigger:checked:after {
  -webkit-transform: rotate(45deg) translate3d(0, -2px, 0);
          transform: rotate(45deg) translate3d(0, -2px, 0); }

.trigger:checked:before {
  -webkit-transform: rotate(-45deg) translate3d(-2px, 3px, 0);
          transform: rotate(-45deg) translate3d(-2px, 3px, 0); }

.toggle-sources__label {
  width: auto;
  text-align: left;
  line-height: 20px;
  text-transform: lowercase;
  font-size: 12px;
  font-weight: 500;
  text-decoration: underline;
  display: inline-block;
  z-index: 9;
  opacity: 0.3;
  padding: 20px 30px 20px 45px;
  background-position: center right;
  background-repeat: no-repeat;
  background-size: 15px;
  background-image: url(data:image/svg+xml;base64,);
  -webkit-transition-duration: 0.25s;
       -o-transition-duration: 0.25s;
          transition-duration: 0.25s; }

.toggle-sources__input {
  visibility: hidden;
  height: 0px;
  width: 0px;
  margin: 0;
  padding: 0;
  display: none; }

.toggle-sources__input:checked + label {
  background-image: url(data:image/svg+xml;base64,); }

.toggle-sources__input:checked ~ div {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0); }

.lastupdated {
  position: absolute;
  top: 20px;
  right: 22px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.3); }

@media (max-width: 767px) {
  .lastupdated {
    right: 60px; } }

.poweredby {
  width: 100%;
  text-align: center;
  margin: 30px 0 10px 0;
  display: inline-block;
  color: rgba(0, 0, 0, 0.3);
  font-size: 14px; }

.poweredby a {
  color: rgba(0, 0, 0, 0.3);
  text-decoration: underline; }

/* Slideout Menu */
.slideout-menu {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  width: 270px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  display: none; }

.slideout-panel {
  width: calc(100% - 270px);
  margin-left: 30%;
  float: right;
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
  position: relative;
  z-index: 1;
  will-change: transform;
  padding-left: 50px;
  display: inline-block;
  height: 100%;
  background: whitesmoke; }

@media (max-width: 767px) {
  .slideout-panel {
    width: 100%;
    margin-left: 0; } }

.slideout-panel:before {
  content: "";
  width: 48px;
  height: 100%;
  display: block;
  background: lightgray;
  position: absolute;
  left: 0;
  top: 0;
  background: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0.06)), to(whitesmoke));
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.06), whitesmoke);
  background: -o-linear-gradient(left, rgba(0, 0, 0, 0.06), whitesmoke);
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.06), whitesmoke);
  border-left: 1px solid rgba(0, 0, 0, 0.13);
  border-right: 1px solid rgba(0, 0, 0, 0.01); }

